
import { defineComponent, ref } from "vue";
import { POSInfoDetail, ICM21700112_REQ_BODY, ICM21700112_RES, AttachInfoList, ContentList } from "@/shared/type/ICM21700112.type";
import { ICM21700121_RES, ICM21700121_REQ_BODY } from "@/shared/type/ICM21700121.type";
import { ICM21700131_REQ_BODY, ICM21700131_RES } from "@/shared/type/ICM21700131.type";
import { ICM21700141_REQ_BODY, ICM21700141_RES } from "@/shared/type/ICM21700141.type";
import { ICM21500111_REQ_BODY, ICM21500111_RES } from "@/shared/type/ICM21500111.type";
import { modalController } from '@ionic/vue';
import { FileSize } from "@/shared/services/FileSizeService";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import PhoneInput from '@/shared/components/PhoneInput/PhoneInput.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";
import { FILE_TYPE } from "@/shared/constants/common.const";

const fileSize = new FileSize();
export default defineComponent({
    name: "icm215001sc01",
    components: {
        baseTab,
        basePan,
        PhoneInput,
        BreadCrumb,
        ModifyCard,
        SelectOption
    },
    data() {
        const input = {
            pOSMerchantTypeCode: "",
            firstRegisterTellerName: "",
            lastChangeTime: "",
            lastChangeTellerID: "",
            latitude: "",
            lastChangeTellerName: "",
            firstRegisterTellerID: "",
            staffPhoneNo: "",
            discountRate: "",
            pOSMerchantCode: "",
            deleteBranchCode: "",
            pOSMerchantPhoneNo: "",
            displayYN: "",
            longitude: "",
            customerNo: "",
            lastChangeDate: "",
            deleteTellerID: "",
            deleteTime: "",
            firstRegisterBranchCode: "",
            deleteDate: "",
            firstRegisterTime: "",
            lastChangeBranchCode: "",
            firstRegisterDate: "",
            deleteTellerName: "",
            attachInfoList: [] as AttachInfoList[],
            contentList: [] as ContentList[]
        } as POSInfoDetail
        const modifyCard = {
            registerTellerId: '',
            registerTellerName: '',
            registerDate: '',
            registerTime: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: ''
        }
        const viewType = this.$route.query.type
        const fileSize = new FileSize();
        const isInvalid = false;
        const headersIcon = [
            { key: "En", name: "common.english" },
            { key: "Km", name: "common.khmer" },
            { key: "Ko", name: "common.korean" },
            // { key: "Ja", name: "common.japanese" },
            // { key: "Zh", name: "common.chinese" },
        ];
        const boardPOSLangDefaultValue = ref<Array<ContentList>>([
            { languageCode: "common.english", pOSMerchantName: "", pOSMerchantAddress: "", pOSMerchantDesc: "" },
            { languageCode: "common.khmer", pOSMerchantName: "", pOSMerchantAddress: "", pOSMerchantDesc: "" },
            { languageCode: "common.korean", pOSMerchantName: "", pOSMerchantAddress: "", pOSMerchantDesc: "" },
            { languageCode: "common.japanese", pOSMerchantName: "", pOSMerchantAddress: "", pOSMerchantDesc: "" },
            { languageCode: "common.chinese", pOSMerchantName: "", pOSMerchantAddress: "", pOSMerchantDesc: "" }
        ])
        const fileInfo = {
            image: '',
            imgSize: '',
            imageName: ''
        }
        return {
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
            isDisableImgUpload: true,
            headersIcon,
            boardPOSLangDefaultValue,
            input,
            viewType,
            fileInfo,
            isEditable: true,
            isFaqCategoryCode: true,
            pOSMerchantCode: this.$route.query.pOSMerchantCode,
            getImage: null,
            errorMessage: "",
            nameFile: "FAQ List.pdf",
            isInvalid,
            fileSize,
            isValidated: false,
            isMerchantCode: true,
            modifyCard,
            isInvalidLatitude: false,
            isInvalidLongitude: false,
            isImageEmty: false,
            isInvalidPOSMerchantPhoneNo: false,
            isInvalidStaffPhoneNo: false,
        }
    },
    computed: {
         YnType(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
        pOSMerchantTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.POSMerchantTypeCode
        },
        displayName(): string {
            return this.viewType === "edit"
                ? this.$t("icm202002sc01.pos_merchant_edit")
                : this.$t("icm202002sc01.pos_merchant_register");
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },
    async mounted() {
        if (this.approvalType === '') {
            if (this.viewType === "edit") {
                this.POSInfoMerchantDetail();
                this.isEditable = true;
                this.isMerchantCode = true
            } else {
                this.input.displayYN = this.YnType[0].key
                this.isFaqCategoryCode = true
                this.isEditable = false;
                this.isMerchantCode = false
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') { 
                    const body = { pOSMerchantCode: authDetailParsed.pOSMerchantCode } as ICM21700112_REQ_BODY;
                    const res = await this.$request.getRequest("ICM21700112", body, false) as ICM21700112_RES;
                    const contentsList = this.convertCommonCode(res?.body.contentList)
                    for (let i = 0; i < contentsList.length; i++) {
                        this.boardPOSLangDefaultValue.map((item) => {
                            if (item.languageCode === contentsList[i].languageCode) {
                                item.pOSMerchantName = contentsList[i].pOSMerchantName;
                                item.pOSMerchantAddress = contentsList[i].pOSMerchantAddress;
                                item.pOSMerchantDesc = contentsList[i].pOSMerchantDesc;
                            }
                        })
                    }
                    this.input.pOSMerchantTypeCode = authDetailParsed.pOSMerchantTypeCode
                    this.input.latitude = authDetailParsed.latitude
                    this.input.staffPhoneNo = authDetailParsed.staffPhoneNo
                    this.input.discountRate = authDetailParsed.discountRate
                    this.input.pOSMerchantCode = authDetailParsed.pOSMerchantCode
                    this.input.pOSMerchantPhoneNo = authDetailParsed.pOSMerchantPhoneNo
                    this.input.displayYN = authDetailParsed.displayYN
                    this.input.longitude = authDetailParsed.longitude
                    this.input.customerNo = authDetailParsed.customerNo
                    this.input.attachInfoList = [] as AttachInfoList[],
                    this.input.contentList = [] as ContentList[]
                    await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
                } else {
                    const contentsList = this.convertCommonCode(authDetailParsed.contentList)
                    for (let i = 0; i < contentsList.length; i++) {
                        this.boardPOSLangDefaultValue.map((item) => {
                            if (item.languageCode === contentsList[i].languageCode) {
                                item.pOSMerchantName = contentsList[i].pOSMerchantName;
                                item.pOSMerchantAddress = contentsList[i].pOSMerchantAddress;
                                item.pOSMerchantDesc = contentsList[i].pOSMerchantDesc;
                            }
                        })
                    }
                    this.input.pOSMerchantTypeCode = authDetailParsed.pOSMerchantTypeCode
                    this.input.latitude = authDetailParsed.latitude
                    this.input.staffPhoneNo = authDetailParsed.staffPhoneNo
                    this.input.discountRate = authDetailParsed.discountRate
                    this.input.pOSMerchantCode = authDetailParsed.pOSMerchantCode
                    this.input.pOSMerchantPhoneNo = authDetailParsed.pOSMerchantPhoneNo
                    this.input.displayYN = authDetailParsed.displayYN
                    this.input.longitude = authDetailParsed.longitude
                    this.input.customerNo = authDetailParsed.customerNo
                    this.input.attachInfoList = [] as AttachInfoList[],
                    this.input.contentList = [] as ContentList[]
                    await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
                }
            })
        }
    },

   watch: {
        // Check Validation Phone Number
        'input.staffPhoneNo': function (newValue) {
            if (newValue.length == 0) {
                this.isInvalidStaffPhoneNo = false
            } else if (newValue.length == 9 || newValue.length > 9) {
                this.isInvalidStaffPhoneNo = false
            }
            else if (newValue.length < 9) {
                this.isInvalidStaffPhoneNo = true
            }
        },
        'input.pOSMerchantPhoneNo': function (newValue) {
            if (newValue.length == 0) {
                this.isInvalidPOSMerchantPhoneNo = false
            }else if(newValue.length == 9 || newValue.length > 9){
                this.isInvalidPOSMerchantPhoneNo = false
            }else if (newValue.length < 9){
                this.isInvalidPOSMerchantPhoneNo = true
            }
        },
        // Check Validation Map 
         'input.latitude'(newValue) {
            if (this.$validate.validateLatitude(newValue)) {
                this.isInvalidLatitude = true
            } else {
                this.isInvalidLatitude = false
            }
        },
        'input.longitude'(newValue) {
            if (this.$validate.validateLongitude(newValue)) {
                this.isInvalidLongitude = true
            } else {
                this.isInvalidLongitude = false
            }
        }
    },
    methods: {
        handleMap() {
            window.open(`https://www.google.com/maps/search/?api=1&query=${this.input.latitude},${this.input.longitude}`);
        },
        // Checl CustomerNo Validation (CIB20100114)
        async getCustomerValidation() {
            const body: ICM21500111_REQ_BODY = {
                customerNo: this.input.customerNo
            }
            // ---- Adapter: CIB20100114_Adapter_POSMerchant_ValidateCustomerNo
            const resBody = await this.$request.getRequest('CIB20100114', body, false) as ICM21500111_RES

            if (resBody.body.exitYN === 'Y') {
                this.$dialog.onOpen({
                    content: `${this.$t("message.duplicate_customer_no")}`,
                    dialogMode: "Question",
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            } else {
                this.$dialog.onOpen({
                    content: `${this.$t("message.success")}`,
                    dialogMode: "Question",
                    callback: () => {
                        this.isValidated = true
                        modalController.dismiss()
                    }
                });
            }
        },
        handleCancel() {
            this.$dialog.onOpen({
                content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                dialogMode: 'Question',
                callback: () => {
                    this.onConfirmCancel()
                }
            });
        },
        onConfirmCancel() {
            modalController.dismiss()
            if (this.viewType === "register") {
                this.$router.push('/ICM215001SC00')
            }
            else {
                this.fileInfo.imageName = this.fileInfo.imgSize = ''
                this.isEditable = true;
                this.isInvalid = false
                this.isInvalidStaffPhoneNo = false
                this.isInvalidPOSMerchantPhoneNo = false
                this.POSInfoMerchantDetail();
            }
        },
        handleList() {
            this.$router.push('/ICM215001SC00')
        },
        handleEdit() {
            this.isEditable = false;
        },
        handleDelete() {
            this.$dialog.onOpen({
                content: this.$t("message.are_you_sure_you_want_to_delete"),
                dialogMode: 'Question',
                callback: () => {
                    this.merhcantPOSDelete();
                    modalController.dismiss();
                },
            });
        },
        // POS Delete TR ICM21700141
        async merhcantPOSDelete() {
            const reqBody: ICM21700141_REQ_BODY = {
                pOSMerchantCode: this.input.pOSMerchantCode
            }
            const res: any = await this.$request.getRequest("ICM21700141", reqBody, false) as ICM21700141_RES;
            if (res?.header.result) {
                this.$router.push('/ICM215001SC00')
            }
        },
        handleSave () {
            //     // check validation language EN
            var item: any = this.boardPOSLangDefaultValue.find(data => data.languageCode == 'common.english')
            if (this.input.pOSMerchantPhoneNo.length > 8 && this.input.staffPhoneNo.length > 8 &&
                this.input.latitude && this.input.longitude && item.pOSMerchantName && 
                this.input.pOSMerchantCode && 
                this.input.displayYN && 
                this.input.discountRate && 
                this.input.customerNo &&
                this.input.pOSMerchantTypeCode ){
                     this.$dialog.onOpen({
                            content: this.$t('message.would_you_like_to_save'),
                            dialogMode: 'Question',
                            callback: () => {
                                if(this.viewType === "edit"){
                                    this.POSMerchantUpdate();
                                }else{
                                    this.POSMerchantRegister();
                                }
                            }
                        })
            }else{
                this.isInvalidPOSMerchantPhoneNo = true
                this.isInvalidStaffPhoneNo = true
                this.isInvalid = true
            }
        },
        // POS Register TR (ICM21700121)
        async POSMerchantRegister() {
            if (this.getImage !== null) {
                const res = await this.$file.uploadFile([this.getImage], "ICM");
                for(let i = 0; i < res.data.body.fileList.length; i++) {
                    this.input.attachInfoList.push({
                        filePath: res.data.body.fileList[i].fileContentType,
                        attachTypeCode: FILE_TYPE.IMAGE,
                        fileId: res.data.body.fileList[i].fileId
                    })
                }
            }

            const body = {
                displayYN: this.input.displayYN,
                pOSMerchantCode: this.input.pOSMerchantCode,
                staffPhoneNo: this.input.staffPhoneNo,
                customerNo: this.input.customerNo,
                pOSMerchantTypeCode: this.input.pOSMerchantTypeCode,
                pOSMerchantPhoneNo: this.input.pOSMerchantPhoneNo,
                discountRate: Number(this.input.discountRate),
                latitude: Number(this.input.latitude),
                longitude: Number(this.input.longitude),
                attachInfoList: this.input.attachInfoList,
                contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardPOSLangDefaultValue))),
            } as ICM21700121_REQ_BODY
            const res: any = await this.$request.getRequest("ICM21700121", body, false) as ICM21700121_RES;

            if (res.header.result) {
                this.$router.push('/ICM215001SC00')
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },
        // POS Update TR (ICM21700131)
        async POSMerchantUpdate() {
            if (this.getImage !== null && !this.isImageEmty) {
                if(this.input.attachInfoList.length === 0) {
                    const res = await this.$file.uploadFile([this.getImage], "ICM")
                    for(let i = 0; i < res.data.body.fileList.length; i++) {
                        const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                        this.input.attachInfoList.push({
                            filePath: resFileInfo.data.body.filePath,
                            attachTypeCode: FILE_TYPE.IMAGE,
                            fileId: res.data.body.fileList[i].fileId
                        })
                    }
                } else if(this.input.attachInfoList.length > 0) {
                    if(this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
                        const res = await this.$file.uploadFile([this.getImage], "ICM")
                        for (let index = 0; index < this.input.attachInfoList.length; index++) {
                        if(this.input.attachInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                            await this.$file.updateFileStatus(this.input.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                            for(let i = 0; i < res.data.body.fileList.length; i++) {
                                await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v => 
                                    Object.assign(this.input.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                            )
                            }
                        }
                    }
                    } else {
                        const res = await this.$file.uploadFile([this.getImage], "ICM")
                        for(let i = 0; i < res.data.body.fileList.length; i++) {
                            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                            this.input.attachInfoList.push({
                                filePath: resFileInfo.data.body.filePath,
                                attachTypeCode: FILE_TYPE.IMAGE,
                                fileId: res.data.body.fileList[i].fileId
                            })
                        }
                    }
                }
            }
            if (this.getImage === null && this.isImageEmty) {
                for(let i = 0; i < this.input.attachInfoList.length; i++) {
                    if (this.input.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
                        await this.$file.updateFileStatus(this.input.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
                        const index = this.input.attachInfoList.indexOf(this.input.attachInfoList[i]);
                        this.input.attachInfoList.splice(index, 1);
                    }
                }
            }
            const body = {
                customerNo: this.input.customerNo,
                pOSMerchantCode: this.input.pOSMerchantCode,
                staffPhoneNo: this.input.staffPhoneNo,
                discountRate: Number(this.input.discountRate),
                pOSMerchantTypeCode: this.input.pOSMerchantTypeCode,
                contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardPOSLangDefaultValue))),
                latitude: Number(this.input.latitude),
                displayYN: this.input.displayYN,
                pOSMerchantPhoneNo: this.input.pOSMerchantPhoneNo,
                attachInfoList: this.input.attachInfoList,
                longitude: Number(this.input.longitude),
            } as ICM21700131_REQ_BODY
            
            const res: any = await this.$request.getRequest("ICM21700131", body, false) as ICM21700131_RES;

            if (res.header.result) {
                this.isEditable = true
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.POSInfoMerchantDetail();
                        modalController.dismiss()
                        this.POSInfoMerchantDetail();
                    }
                });
            }
            return res.header.result
        },
        // POS Merchant Upload Image
        handleUploadImage(event: any) {
            this.isImageEmty = false
            const accectFile = /(\.png)$/i;
            const files = event.target.files;
            const file = files[0];
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else if (file.size > 2097152) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_2MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getImage = file;
                const size = fileSize.countFileSize(file.size);
                this.fileInfo.imgSize = size;
                const reader = new FileReader();
                reader.readAsDataURL(file);
                this.fileInfo.imageName = file.name;
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result);
                };
            }
            const duplicatedFile = document.getElementById('btn02 btn_upload') as HTMLInputElement;
            duplicatedFile.value = ''
        },
        // Remove Image Upload
       removeFile() {
            this.getImage = null
            this.isImageEmty = true
            this.fileInfo.imageName = "";
            this.fileInfo.imgSize = ""
        },

        onKeyDown(event: any) {
            this.$validate.validateNumber(event)
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        },
        // Check Validation input Late and Long 
        validateLate(event: KeyboardEvent) {
                  const charCode = (event.which) ? event.which : event.keyCode;
                  if (
                        ((event.target as HTMLInputElement).value.length >= 11)
                        && charCode !== 8
                  ) {
                        return event.preventDefault()
                  }
            },

        validateLong(event: KeyboardEvent) {
                  const charCode = (event.which) ? event.which : event.keyCode;
                  if (((event.target as HTMLInputElement).value.length >= 12) && charCode !== 8) {
                        return event.preventDefault();
                  }
            },
    //  Convert Language 
        convertCommonCode(listArr: ContentList[]): ContentList[] {
            const convertLangCode = listArr.map((item: ContentList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : "";
                return item;
            });
            return convertLangCode;
        },

        convertLangToCode(listsArr: ContentList[]): ContentList[] {
            const data = listsArr.map((item: ContentList) => {
                item.languageCode = this.$codeConverter.convertValue( "LanguageCodeTranslate", item.languageCode, "value");
                return item;
            });
            return data;
        },

        // POS getDetail TR (ICM21700112)
        async POSInfoMerchantDetail() {
            modalController.dismiss()
            const body = {
                pOSMerchantCode: this.$route.query.pOSMerchantCode
            }
            const res: any = await this.$request.getRequest("ICM21700112", body, false) as ICM21700112_RES;
                  this.modifyCard = {
                registerTellerId: res?.body.firstRegisterTellerID,
                registerTellerName: res?.body.firstRegisterTellerName,
                registerDate: res?.body.firstRegisterDate,
                registerTime: res?.body.firstRegisterTime,
                updatedTellerId: res?.body.lastChangeTellerID,
                updatedTellerName: res?.body.lastChangeTellerName,
                updatedDate: res?.body.lastChangeDate,
                updatedTime: res?.body.lastChangeTime
            }
            const contentsList = this.convertCommonCode(JSON.parse(JSON.stringify(res.body.contentList)))
            for (let i = 0; i < contentsList.length; i++) {
                this.boardPOSLangDefaultValue.map((item) => {
                    if (item.languageCode === contentsList[i].languageCode) {
                        item.pOSMerchantName = contentsList[i].pOSMerchantName;
                        item.pOSMerchantAddress = contentsList[i].pOSMerchantAddress;
                        item.pOSMerchantDesc = contentsList[i].pOSMerchantDesc;
                    }
                })
            }
            this.input.attachInfoList = res.body.attachInfoList.map((item) => {
                return {
                    attachTypeCode: item.attachTypeCode,
                    fileId: item.fileId,
                    filePath: item.filePath
                }
            })
            if (res.header.result) {
                this.input = res.body
            }
             this.requestFileDetail("02");
        },
        //Funstion Detail Image 
        async requestFileDetail(attachTypeCode: string) {
            for (let i = 0; i < this.input.attachInfoList.length; i++) {
                if (this.input.attachInfoList[i].attachTypeCode == attachTypeCode) {
                    const res = await this.$file.getFileInfo(this.input.attachInfoList[i].fileId)
                    if (attachTypeCode == '02') {
                        this.fileInfo.imageName = res?.data.body.originalFileName
                        this.fileInfo.imgSize = res?.data.body.fileSizeString
                        const resImage = await this.$file.previewImage(this.input.attachInfoList[i].fileId)
                        this.fileInfo.image = resImage
                    }
                }
            }
        },

        isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
            const isFileAlreadyExisted = this.input.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
            return isFileAlreadyExisted
        },
    }
})
